import {View} from './View';
import {tomadaDeTempoCurrentInstance} from '../controllers/TomadaDeTempoController';

export class TomadaDeTemposView extends View {
    
    constructor(elemento) {
        super(elemento);

        elemento.addEventListener('click', function(event) {

            if(event.target.nodeName == 'TH')
                tomadaDeTempoCurrentInstance().ordena(event.target.textContent.toLowerCase());    
        });
    }

    template(model) {
        return `
        <table class="table table-hover table-bordered">
        
            <thead>
                <tr>
                    <th>Corrida</th>
                    <th>Piloto</th>
                    <th>Tempo</th>
                </tr>
            </thead>
        
            <tbody>
                ${model.tomadaDeTempos.map(n => `
                    
                    <tr>
                        <td>${n.corrida}</td>
                        <td>${n.piloto}</td>
                        <td>${n.tempo}</td>
                    </tr>
                    
                `).join('')}                
            </tbody>
        </table>
        `;
    }
}
